IG$.cVis.cartesian = $s.extend(IG$.cVis._highcharts, {
    charttype: "cartesian",
	subtype: null,

	spacingBottom: 1,

    _create_option: function(mresult) {
        var me = this,
			mcr = me.chart_option,
			rcs = me.subtype,
			bfixedp = (rcs == "fixedplacement"),
			coption = {},
			titleposition = mcr.titleposition || "",
            c_cset = mcr.colorset || mresult.c_cset;

        if (IG$.__chartoption && IG$.__chartoption.chartcolors && IG$.__chartoption.chartcolors[c_cset])
		{
			coption.colors =  IG$.__chartoption.chartcolors[c_cset];
		}

        coption.chart = {
			animation: false,
			renderTo: me.container,
			defaultSeriesType: (bfixedp ? null : "area"),
			reflow: false,
			alignTicks: true,
			borderWidth: 0,
			spacingBottom: me.spacingBottom
		};

        if (mcr.showtitle == true)
		{
			coption.title = {text: mcr.title};

			if (titleposition.indexOf("BOTTOM_") > -1)
			{
				coption.title.floating = true;
				coption.chart.marginBottom  = 50;
				coption.title.y = h - 30;
			}

			if (titleposition.indexOf("_LEFT") > -1)
			{
				coption.title.align = "left";
			}
			else if (titleposition.indexOf("_RIGHT") > -1)
			{
				coption.title.align = "right";
			}
		}
		else
		{
			coption.title = {
				text: ""
			};
		}

        return coption;
    },

    _createAxis: function(coption) {
		var me = this,
			mcr = me.chart_option,
			xticks = me.xticks;

        if (mcr.enablezoom && !mcr.enabledragselect)
        {
            coption.chart.zoomType = "xy";
        }
        
        var xlabels = {
            labels: {
                maxStaggerLines: 2,
                formatter: function() {
                    // var s = (this.value.split ? this.value.split(sep) : [this.value]);
                    // return s.join("<br>");
                    return this.value;
                }
            } //, rotation: 320
        };
        
        if (mcr.xlabel == false)
        {
            xlabels.labels.enabled = false;
        }
        else
        {
            if (mcr.xaxisrot == false)
            {
                xlabels.labels.autoRotation = false;
                
                if (mcr.xstagl > 0)
                {
                    xlabels.labels.staggerLines = mcr.xstagl;
                    xlabels.labels.maxStaggerLines = mcr.xstagl;
                }
                
                if (mcr.xstep > 1)
                {
                    xlabels.labels.step = mcr.xstep;
                }
            }
        }
        
        coption.xAxis = {
            categories: xticks,
            labels: xlabels.labels,
            showLastTickLabel: true,
            align: "left",
            events: {
                setExtremes: function (event) {
                    if (Math.abs(this.options.labels.rotation) == 90)
                    {
                        var labelWidth = parseInt(this.options.labels.style.lineHeight) + 2,
                            plotAreaWidth = parseInt(this.chart.plotBox.width),
                            labelsCount = Math.floor(plotAreaWidth / labelWidth),
                            pointsCount,
                            step;

                        if (event.max !== null && event.max !== undefined)
                        {
                            pointsCount = Math.round(event.max - event.min);
                        }
                        else
                        {
                            pointsCount = Math.round(this.dataMax - this.dataMin);
                        }

                        step = Math.ceil(pointsCount / (labelsCount * (this.tickInterval == null ? 1 : this.tickInterval)));

                        this.update({
                            labels: {
                                step: step
                            }
                        }, true);
                    }
                }
            }
        };
    
        if (mcr.e3d_en === true)
		{
			coption.chart.options3d = {
				enabled: true,
				alpha: Number(mcr.e3d_al),
				beta: Number(mcr.e3d_be),
				depth: Number(mcr.e3d_de),
				viewDistance: Number(mcr.e3d_vd)
			};
		}
    },

    _createYAxis: function(coption) {
        var me = this,
			mcr = me.chart_option,
			cnt,
			mseq,
			p_off = 0,
			ylabels = {
			formatter: function() {
				// ###.####
				var yaxisformat = mcr.yaxisformat,
					p = 0,
					value = this.value, 
					postfix = "",
					pval, n, i,
					c;
				
				if (yaxisformat)
				{
					n = yaxisformat.indexOf(".");
					if (n > -1)
					{
						for (i=n+1; i < yaxisformat.length; i++)
						{
							c = yaxisformat.charAt(i);
							if (c == "#" || c == "0")
							{
								p++;
							}
						}
					}
					
					n = yaxisformat.indexOf("%");
					if (n > -1)
					{
						pval = (n-1) > 0 ? yaxisformat.charAt(n-1) : null;
						
						if (pval == "'")
						{
							
						}
						else
						{
							value = value * 100;
						}
						
						postfix = " %";
					}
				}
				
				return value + postfix; // Highcharts.numberFormat(value, p) + postfix;
			}
		};

		var ytitle = {
			text: mcr.yaxistitle || null
		};
		coption.yAxis = {title: ytitle, labels: ylabels};

		if (mcr.yaxismax != "" && IG$.isNumber(mcr.yaxismax) == true)
		{
			coption.yAxis.max = Number(mcr.yaxismax);
		}

		if (mcr.yaxismin != "" && IG$.isNumber(mcr.yaxismin) == true)
		{
			coption.yAxis.min = Number(mcr.yaxismin);
		}
		
		if (mcr.ytickint > 0)
		{
			coption.yAxis.tickInterval = mcr.ytickint;
		}

		if (me.bpolarchart)
		{
			coption.yAxis.gridLineInterpolation = "polygon";
			coption.yAxis.lineWidth = 0;
		}
		
		coption.legend = {
			margin: 2
		};

		if (mcr.showlegend == true)
		{
			coption.legend.enabled = true;
			mcr.legendposition = mcr.legendposition || "BOTTOM_CENTER";

			coption.legend.verticalAlign = mcr.legendposition.indexOf("BOTTOM_") > -1 ? "bottom" :
				(mcr.legendposition.indexOf("TOP_") > -1 ? "top" : "middle");
			coption.legend.align = (mcr.legendposition.indexOf("LEFT_") > -1) ? "left" :
				((mcr.legendposition.indexOf("RIGHT_") > -1) ? "right" : "center");
			if (coption.legend.align == "center")
			{
				if (mcr.legendposition.indexOf("_RIGHT") > -1)
				{
					coption.legend.x = 200;
				}
				else if (mcr.legendposition.indexOf("_LEFT") > -1)
				{
					coption.legend.x = - w / 2 + 200;
				}

				coption.legend.layout = "horizontal";
			}
			else
			{
				if (mcr.legendposition.indexOf("_TOP") > -1)
				{
					coption.legend.verticalAlign = "top";
				}
				else if (mcr.legendposition.indexOf("_BOTTOM") > -1)
				{
					coption.legend.verticalAlign = "bottom";
				}

				coption.legend.layout = "vertical";
			}
		}
		else
		{
			coption.legend.enabled = false;
		}
		
		if (mcr.lgndfloating)
		{
			coption.legend.floating = true;
			coption.legend.borderWidth = 1;
			coption.legend.backgroundColor = "#FFFFFF"; // (Highcharts.theme && Highcharts.theme.legendBackgroundColor) || "#FFFFFF";
		}
    },

    _plot_chart: function(coptions, coption) {
        var me = this,
			mcr = me.chart_option,
			marker = {
				enabled: mcr.dl_marker ? true : false, 
				states: {
					hover: {
						enabled: true
					}
				}
			},
			rcs = me.rcs,
			dobj,
			options3d = coption.chart ? coption.chart.options3d : null,
			bfixedp = (rcs == "fixedplacement"),
			measures = me.sheetoption.model.measures,
			bsync_l, bsync,
			series = me.series;

		if (rcs == "syncchart")
		{
			bsync_l = (measures && measures.length > 1) ? measures.length : -1;
			bsync = bsync_l > 1 ? 1 : 0;
		}
    
        for (i=0; i < series.length; i++)
        {
            mseq = (mcr.renderbymeasures ? series[i].meas_k : i);
            
            if (!bfixedp)
            {
                if (mseq > -1 && mcr.renderas && mcr.renderas.length > mseq && mcr.renderas[mseq])
                {
                    series[i].type = IG$.getSeriesType(mcr.renderas[mseq]);
                }
            }
            
            if (mseq > -1 && mcr.dl_enable_l && mcr.dl_enable_l.length > mseq)
            {
                if (mcr.dl_enable_l[mseq] == "F")
                {
                    if (series[i].dataLabels)
                    {
                        series[i].dataLabels = {
                            enabled: false
                        };
                    }
                }
                else if (mcr.dl_enable_l[mseq] == "T")
                {
                    if (!series[i].dataLabels)
                    {
                        series[i].dataLabels = {
                            enabled: true,
                            align: mcr.dl_align || "center",
                            inside: mcr.dl_inside,
                            formatter: function() {
                                return this.point.fval || this.y; //  || Highcharts.numberFormat(this.y, 0);
                            }
                        };
                    }
                }
            }
            
            if (mcr.dl_marker_l && mcr.dl_marker_l.length > mseq)
            {
                if (mcr.dl_marker_l[mseq] == "F")
                {
                    if (series[i].marker)
                    {
                        series[i].marker = {
                            enabled: false
                        };
                    }
                }
                else if (mcr.dl_marker_l[mseq] == "T")
                {
                    if (!series[i].marker)
                    {
                        series[i].marker = {
                            enabled: true, 
                            states: {
                                hover: {
                                    enabled: true
                                }
                            }
                        };
                    }
                }
            }
            
            if (mcr.dl_linetype_l && mcr.dl_linetype_l.length > mseq && mcr.dl_linetype_l[mseq])
            {
                series[i].dashStyle = me.getDashStyle(mcr.dl_linetype_l[mseq]);
            }

            // if (me.rcs == "waterfall")
            // {
                // series[i].upColor = Highcharts.getOptions().colors[2];
                // series[i].color = Highcharts.getOptions().colors[3];
            // }
        }

        if (mcr.usedualaxis == true && mcr.dualaxisitem && mcr.dualaxisitem.length > -1 && series.length > 1)
        {
            for (i=0; i < series.length; i++)
            {
                mseq = (mcr.renderbymeasures ? series[i].meas_k : i);
                
                if (mcr.dualaxisitem.length > mseq && mcr.dualaxisitem[mseq] == "T")
                {
                    hasdualaxis = true;
                    break;
                }
            }

            if (hasdualaxis == true)
            {
                var dyax = {
                    title: {
                        text: mcr.yaxistitle2 || null,
                        enabled: mcr.yaxistitle ? true : false
                    },
                    opposite: true,
                    labels: {
                        formatter: function() {
                            var yaxisformat = mcr.yaxisformat2,
                                p = 0;
                            if (yaxisformat && yaxisformat.indexOf(".") > -1)
                            {
                                p = yaxisformat.length - yaxisformat.lastIndexOf(".") - 1;
                            }
                            return Highcharts.numberFormat(this.value, p);
                        }
                    }
                };

                if (mcr.yaxismax2 != "" && IG$.isNumber(mcr.yaxismax2) == true)
                {
                    dyax.max = Number(mcr.yaxismax2);
                }

                if (mcr.yaxismin2 != "" && IG$.isNumber(mcr.yaxismin2) == true)
                {
                    dyax.min = Number(mcr.yaxismin);
                }
                coption.yAxis = [coption.yAxis];
                coption.yAxis.push(dyax);
                
                for (i=0; i < series.length; i++)
                {
                    mseq = (mcr.renderbymeasures ? series[i].meas_k : i);
                    
                    if (mcr.dualaxisitem.length > mseq && mcr.dualaxisitem[mseq] == "T")
                    {
                        series[i].yAxis = 1;
                    }
                }
            }
        }

        coption.tooltip = {
    //					formatter: function() {
    //						return this.point.name + "<br>" + Highcharts.numberFormat(this.y, mcr.precision || 0) + " (" + Highcharts.numberFormat(this.percentage, 1) + " %)";
    //					}

            formatter: function() {
                var r,
                    i;
                if (bfixedp)
                {
                    if (this.points && this.points.length)
                    {
                        r = this.points[0].x + "<br>";
                        for (i=0; i < this.points.length; i++)
                        {
                            r += (i > 0 ? "<br>" : "") + "<b>" 
                                + this.points[i].series.name + "</b> " 
                                + (this.points[i].fval || this.points[i].y); // Highcharts.numberFormat(this.points[i].y, 0));
                        }
                    }
                }
                else if (me.bpointchart)
                {
                    r = this.series.name + "<br>" 
                        + (this.point ? this.point.name || "" : "") + "<br>" 
                        + "X: " + this.x + "<br>" 
                        + "Y: " + (this.point.fval || this.y); // Highcharts.numberFormat(this.y, 0));
                }
                else
                {
                    r = this.series.name + "<br>" 
                        + this.x + "<br>" 
                        + (this.point.fval || this.y); // Highcharts.numberFormat(this.y, 0));
                }
                return r;
            }
        };
        
        coption.plotOptions = {};
        coption.plotOptions.column = {
            turboThreshold: mcr.maxchartresult || 1000,
            allowPointSelect: true,
            events: {
                click: function(param) {
                    eventowner.procClickEvent.call(eventowner, this, param);
                    return false;
                }
            }
        };

        coption.plotOptions.area = {
            turboThreshold: mcr.maxchartresult || 1000,
            allowPointSelect: true,
            marker: marker,
            events: {
                click: function(param) {
                    eventowner.procClickEvent.call(eventowner, this, param);
                }
            }
        };
        
        coption.plotOptions.areaspline = {
            turboThreshold: mcr.maxchartresult || 1000,
            allowPointSelect: true,
            marker: marker,
            events: {
                click: function(param) {
                    eventowner.procClickEvent.call(eventowner, this, param);
                }
            }
        };

        coption.plotOptions.line = {
            turboThreshold: mcr.maxchartresult || 1000,
            allowPointSelect: true,
            marker: marker,
            events: {
                click: function(param) {
                    eventowner.procClickEvent.call(eventowner, this, param);
                }
            }
        };
        
        coption.plotOptions.spline = {
            turboThreshold: mcr.maxchartresult || 1000,
            allowPointSelect: true,
            marker: marker,
            events: {
                click: function(param) {
                    eventowner.procClickEvent.call(eventowner, this, param);
                }
            }
        };

        coption.plotOptions.bar = {
            turboThreshold: mcr.maxchartresult || 1000,
            allowPointSelect: true,
            events: {
                click: function(param) {
                    eventowner.procClickEvent.call(eventowner, this, param);
                }
            }
        };
        
        coption.plotOptions.bubble = {
            turboThreshold: mcr.maxchartresult || 1000,
            maxSize: 120,
            minSize: 8
        };

        // dobj = coption.plotOptions[coption.chart.defaultSeriesType];
        
        if (mcr.dl_enable)
        {
            $.each(["column", "area", "areaspline", "line", "spline", "bar"], function(i, k) {
                var dobj = coption.plotOptions[k];
                
                dobj.dataLabels = {
                    enabled: true,
                    align: mcr.dl_align || "center",
                    inside: mcr.dl_inside,
                    formatter: function() {
                        return this.point.fval || this.y; // Highcharts.numberFormat(this.y, 0);
                    }
                };
            });
        }

        if (me.bpointchart)
        {
            coption.plotOptions.scatter = {
                allowPointSelect: true,
                turboThreshold: mcr.maxchartresult || 1000,
                marker: {
                    symbol: "circle",
                    radius: 5,
                    states: {
                        hover: {
                            enabled: true
                        }
                    }
                },
                states: {
                    hover: {
                        marker: {
                            enabled: false
                        }
                    }
                }
            }
        }

        if (mcr.bands && mcr.bands.length > 0)
        {
            var yaxis = coption.yAxis.length ? coption.yAxis[0] : coption.yAxis,
                xaxis = coption.xAxis.length ? coption.xAxis[0] : coption.xAxis,
                band,
                pb;

            yaxis.plotBands = [];
            yaxis.plotLines = [];
            
            xaxis.plotBands = [];
            xaxis.plotLines = [];
            
            for (i=0; i < mcr.bands.length; i++)
            {
                bp = mcr.bands[i];
                bp.mresult = mresult;

                if (bp.enabled)
                {
                    band = {
                        label: {
                            text: bp.name,
                            formatter: function() {
                                return bp.name;
                            }
                        },
                        color: bp.color
                    };
                    
                    if (bp.type == "curvefit")
                    {
                        cf.push(bp);
                    }
                    else
                    {
                        if (bp.isxaxis)
                        {
                            if (bp.type == "band")
                            {
                                band.from = me.getBandValue(mresult, bp.value_1, true); // Number(bp.value_1);
                                band.to = me.getBandValue(mresult, bp.value_2, true); // Number(bp.value_2);
                                xaxis.plotBands.push(band);
                            }
                            else
                            {
                                band.value = me.getBandValue(mresult, bp.value_1); // Number(bp.value_1);
                                band.width = Number(bp.borderwidth) || 2;
                                xaxis.plotLines.push(band);
                            }
                        }
                        else
                        {
                            if (bp.type == "band")
                            {
                                band.from = me.getBandValue(mresult, bp.value_1); // Number(bp.value_1);
                                band.to = me.getBandValue(mresult, bp.value_2); // Number(bp.value_2);
                                yaxis.plotBands.push(band);
                            }
                            else
                            {
                                band.value = me.getBandValue(mresult, bp.value_1); // Number(bp.value_1);
                                band.width = Number(bp.borderwidth) || 2;
                                yaxis.plotLines.push(band);
                            }
                        }
                    }
                }
            }
        }

        if (mcr.stack == true && me.rcs != "waterfall")
        {
            switch (this.rcs)
            {
            case "area":
                coption.plotOptions.area.stacking = (mcr.stackperc ? "percent" : "normal");
                break;
            default:
                if (coption.plotOptions.series == null || typeof coption.plotOptions.series == "undefined")
                {
                    coption.plotOptions.series = {};
                }
                coption.plotOptions.series.stacking = (mcr.stackperc ? "percent" : "normal");
                break;
            }
        }

        if (!bfixedp && coption.renderas && coption.renderas.length > 0)
        {
            for (i=0; i < coption.renderas.length; i++)
            {
                if (coption.renderas[i] != "" && series.length > i)
                {
                    var rtype = IG$.getSeriesType(coption.renderas[i])
                    series[i].type = rtype;
                }
            }
        }
        
        if (bsync)
        {
            $(me.container).bind("mousemove touchmove", function (e) {
                var chart,
                    point,
                    i;
                if (!me._masterCharts)
                    return;
                    
                for (i = 0; i < me._masterCharts.length; i = i + 1) {
                    chart = me._masterCharts[i];
                    if (chart)
                    {
                        e = chart.pointer.normalize(e); // Find coordinates within the chart
                        point = chart.series[0].searchPoint(e, true); // Get the hovered point
            
                        if (point) {
                            point.onMouseOver(); // Show the hover marker
                            chart.tooltip.refresh(point); // Show the tooltip
                            chart.xAxis[0].drawCrosshair(e, point); // Show the crosshair
                        }
                    }
                }
            });
            
            var xaxis = coption.xAxis = coption.xAxis || {};
            xaxis.crosshair = true;
            xaxis.events = xaxis.events || {};
            
            // xaxis.events.setExtremes = function(e) {
            // 	var thisChart = this.chart;
            // 
            // 	if (e.trigger !== "syncExtremes") { // Prevent feedback loop
            // 		Highcharts.each(me._masterCharts, function (chart) {
            // 			if (chart !== thisChart) 
            // 			{
            // 				if (chart.xAxis[0].setExtremes) // It is null while updating
            // 				{ 
            // 					chart.xAxis[0].setExtremes(e.min, e.max, undefined, false, { trigger: "syncExtremes" });
            // 				}
            // 			}
            // 		});
            // 	}
            // };
            
            coption.chart.renderTo = null;
            coption.chart.marginLeft = 60; // Keep all charts left aligned
            coption.chart.zoomType = "x";

            $.each(lblnames, function(k, lblname) {
                var cnt = $("<div class='i-sschart-cnt'></div>").appendTo(me.container),
                    ocnt = $(me.container),
                    owidth = IG$.j$ext._w(ocnt),
                    oheight = IG$.j$ext._h(ocnt),
                    ow = owidth / bsync_l,
                    oh = oheight / bsync_l,
                    toption = $.extend(true, {}, coption),
                    serie = [],
                    i;

    //					if (mcr.pielayout == "h")
    //					{
    //						IG$.j$ext._w(cnt, ow);
    //						IG$.j$ext._h(cnt, oheight);
    //						cnt.css({left: p_off});
    //						p_off += ow;
    //					}
    //					else if (mcr.pielayout == "v")
    //					{
                    IG$.j$ext._h(cnt, oh);
                    IG$.j$ext._w(cnt, owidth);
                    cnt.css({top: p_off});
                    p_off += oh;
    //					}

                toption.chart.renderTo = cnt[0];
                
                for (i=0; i < series.length; i++)
                {
                    if (series[i].meas_k == k)
                    {
                        serie.push(series[i]);
                    }
                }

                toption.series = serie;
                coptions.push(toption);
            });
        }
        else
        {
            coption.series = series;
            coptions.push(coption);
        }
    },

    _prep_series_data: function(mresult) {
		var isthumb = false,
			i,
			j,
			data = mresult._tabledata,
			fc = mresult.colfix,
			fr = mresult.rowfix,
			hidden_columns = {}, // mresult.hidden_columns || {},
			hasdualaxis,
			me = this,
			rcs = me.subtype,
			xticks = me.xticks = [],
			n = 1,
			ename,
			series = me.series = [],
			ci,
			labels = [],
			bpointchart = 0,
			bpolarchart = me.bpolarchart,
			bscatter = 0,
			bfixedp = 0,
			bsync = 0,
			bsync_l = -1,
			bdatas,
			cfr,
			scrollX,
			
			mcr = me.sheetoption.model.chart_option,
			swapaxis = mcr.swapaxis,
			useformula = mcr.useformula,
			maxchartresult = mcr.maxchartresult,
			timeseriesfield = mcr.timeseriesfield,
			useformatvalue = mcr.useformatvalue,
			measures = me.sheetoption.model.measures,
			f, _c, _uc, _fc,
			lbl, lbl_i, lbl_c, lbl_k, lblnames = {},
			mtitle,
			cf = [],
			sep = me.getSep(),
			spvalue = me.spvalue = [];

		var tc = (data && data.length > 0) ? data[0].length : 0;
		var tr = (data ? data.length : 0);

		bdatas = Math.min(data.length, (maxchartresult > 0 ? maxchartresult : data.length));
		me._dlen = bdatas;
		cfr = fr;
		
		if (!isthumb)
		{
			if (mresult.pagestart > 0 || mresult.rowcnt > mresult.pageend)
			{
				me.sh = bdatas / mresult.rowcnt;
			}
			else if (maxchartresult > 0 && maxchartresult < data.length)
			{
				me.sh = maxchartresult / data.length;
			}
		}
		else
		{
			scrollX = Math.floor((me.scrollX * mresult.rowcnt) / me.thumbhwidth);
			cfr += scrollX;
			bdatas = Math.min(data.length, bdatas + scrollX);
		}

		me.bdatas = bdatas;

        if (fc+1 < tc && (rcs == "scatter" || rcs == "bubble"))
		{
			var s,
				pvalue;
				//pmax = null,
				// pmin = null,
				// prange = null,
				// zvalue;

			bpointchart = 1;

			if (rcs == "bubble")
			{
				bscatter = 1;
				
//				if (!swapaxis && fc+2 < tc)
//				{
//					bscatter = 1;
//
//					for (i=fr; i < data.length; i++)
//					{
//						zvalue = me.mvalue(data[i][fc+2], useformatvalue) || 0;
//						pmax = (i == fr) ? zvalue : Math.max(pmax, zvalue);
//						pmin = (i == fr) ? zvalue : Math.min(pmin, zvalue);
//					}
//
//					prange = 30 / (pmax - pmin);
//				}
//				else if (swapaxis && fr+2 < tr)
//				{
//					bscatter = 1;
//
//					for (i=fc; i < tc; i++)
//					{
//						zvalue = me.mvalue(data[fr+2][i], useformatvalue) || 0;
//						pmax = (i == fc) ? zvalue : Math.max(pmax, zvalue);
//						pmin = (i == fc) ? zvalue : Math.min(pmin, zvalue);
//					}
//
//					prange = 30 / (pmax - pmin);
//				}
			}

			if (swapaxis)
			{
				for (i=fc; i < Math.min(tc, (maxchartresult > 0 ? maxchartresult : tc)); i++)
				{
					var val = {x: me.mvalue(data[fr][i], useformatvalue), y: me.value(data[fr+1][i], useformatvalue)};

					if (bscatter && fr+2  < tr)
					{
						zvalue = me.mvalue(data[fr+2][i], useformatvalue);
//						zvalue = (zvalue - pmin) * prange + 2;

//						val.marker = {
//							radius: zvalue
//						}
						val.z = zvalue;
					}

					var sname;
					for (j=0; j < fr; j++)
					{
						sname = (j == 0) ? data[j][i].text : sname + sep + data[j][i].text;
					}

					val.name = sname;
					val.datarow = j;

					cvalue = data[0][j].text;

					if (cvalue != pvalue)
					{
						s = new Object();
						s.name = cvalue;
						s.data = [val];
						series.push(s);
					}
					else if (s)
					{
						s.data.push(val);
					}

					pvalue = cvalue;
				}
			}
			else
			{
				for (i=fr; i < Math.min(data.length, (maxchartresult > 0 ? maxchartresult : data.length)); i++)
				{
					var val = {x: me.mvalue(data[i][fc], useformatvalue), y: me.mvalue(data[i][fc+1], useformatvalue)};

					if (bscatter && fc+2  < tc)
					{
						zvalue = me.mvalue(data[i][fc+2], useformatvalue);
//						zvalue = (zvalue - pmin) * prange + 2;

//						val.marker = {
//							radius: zvalue
//						};
						val.z = zvalue;
					}

					var sname;
					for (j=0; j < fc; j++)
					{
						sname = (j == 0) ? data[i][j].text : sname + sep + data[i][j].text;
					}

					val.name = sname;
					val.datarow = i;

					cvalue = data[i][0].text;

					if (cvalue != pvalue)
					{
						s = new Object();
						s.name = cvalue;
						s.data = [val];
						series.push(s);
					}
					else if (s)
					{
						s.data.push(val);
					}

					pvalue = cvalue;
				}
			}
		}
		else
		{
			if (swapaxis == true)
			{
				for (i=fr; i < tr; i++)
				{
					for (j=0; j < fc; j++)
					{
						lbl_i = data[i][j].text || " ";
						lbl = (j==0) ? lbl_i : lbl + lbl_i;
					}
					var s = new Object();
					s.name = lbl;
					s.data = [];
					series.push(s);
					spvalue.push(0);
				}
			}
			else
			{
				for (i=fc; i < tc; i++)
				{
					lbl_k = -1;
					lbl = "";
					if (bsync)
					{
						for (j=0; j < fr; j++)
						{
							lbl_c = data[j][i];
							if (lbl_c.position == 3)
							{
								lbl_k = lbl_c.pindex;
								lblnames[lbl_k] = lbl_c.text;
								continue;
							}
							lbl_i = lbl_c.text;
							lbl = (j==0) ? lbl_i : lbl + sep + lbl_i;
						}
					}
					else
					{
						for (j=0; j < fr; j++)
						{
							lbl_c = data[j][i];
							if (lbl_c.position == 3)
							{
								lbl_k = lbl_c.index;
							}
							lbl_i = lbl_c.text || " ";
							lbl = (j==0) ? lbl_i : lbl + sep + lbl_i;
						}
					}
					var s = new Object();
					s.name = lbl || lblnames[lbl_k];
					s.meas_k = lbl_k;
					s.data = [];
					series.push(s);
					spvalue.push(0);
				}
			}

			var m = Math.max((swapaxis == true ? tc : data.length), 20),
				bformula = false,
				iswaterfall = me.rcs == "waterfall" && mcr.stack == false,
				np,
				_com;

			if (swapaxis == true)
			{
				for (i=fc; i < Math.min(tc, (maxchartresult > 0 ? maxchartresult : tc)); i++)
				{
					bformula = false;

					for (j=0; j < fr; j++)
					{
						ename = (j == 0) ? data[j][i].text : ename + sep + data[j][i].text;
						if (data[j][i].position == 4)
						{
							bformula = true;
							break;
						}
					}
					if (bformula == false || useformula == true)
					{
						ci = 0;
						for (j=fr; j < tr; j++)
						{
							var f = me.mvalue(data[j][i], useformatvalue);
							if (isNaN(f) == true)
							{
								series[ci].data.push(null);
							}
							else
							{
								if (bpolarchart)
								{
									series[ci].data.push(f);
								}
								else
								{
									// series[ci].data.push([ename, f]);
									np = series[ci].data.length;
									series[ci].data.push({
										name: ename,
										x: i - fc,
										y: (iswaterfall && np > 0) ? f - spvalue[ci] : f,
										fval: data[j][i].text
									});

									spvalue[ci] = f;
								}
							}
							ci++;
						}

						xticks.push(ename);
						n++;
					}
				}
			}
			else
			{
				_uc = (tc == fc+1) ? 1 : 0;
				
				for (i=cfr; i < bdatas; i++)
				{
					bformula = false;
					_c = null;
					ename = "";

					for (j=0; j < fc; j++)
					{
						if (hidden_columns[j])
							continue;
						
						ename += (ename ? sep : "") + data[i][j].text;
						if (data[i][j].position == 4)
						{
							bformula = true;
							break;
						}
						_c = _uc && data[i][j].cc ? data[i][j].cc : _c;
					}
					if (bformula == false || useformula == true)
					{
						ci = 0;
						for (j=fc; j < tc; j++)
						{
							f = me.mvalue(data[i][j], useformatvalue);
							
							if (isNaN(f) == true)
							{
								series[ci].data.push({
									name: ename,
									x: i - cfr,
									y: null,
									fval: null
								});
							}
							else
							{
								if (bpolarchart)
								{
									series[ci].data.push(f);
								}
								else
								{
									// series[ci].data.push([ename, f]);
									np = series[ci].data.length;
									_com = {
										name: ename,
										x: i - cfr,
										y: (iswaterfall && np > 0) ? f - spvalue[ci] : f,
										fval: data[i][j].text
									};
									
									if (!_c && data[i][j].cm > -1)
									{
										var colors = coption.colors; // || (Highcharts.theme ? Highcharts.theme.colors : null) || Highcharts.getOptions().colors;
										if (colors)
										{
											_fc = me.interpolate(data[i][j].cm, colors[ci % colors.length]);
										}
									}
									
									if (_c || _fc)
									{
										_com.color = _c || _fc;
									}
									
									_fc = 0;
									
									series[ci].data.push(_com);

									spvalue[ci] = f;
								}
							}
							ci++;
						}

						xticks.push(ename);
						n++;
					}
				}
			}
		}
    },

    draw: function(mresult) {
    	var isthumb = false,
			i,
			j,
			data = mresult._tabledata,
			fc = mresult.colfix,
			fr = mresult.rowfix,
			hidden_columns = {}, // mresult.hidden_columns || {},
			hasdualaxis,
			me = this,

			xticks = [],
			n = 1,
			ename,
			series = [],
			ci,
			labels = [],
			bpointchart = 0,
			bpolarchart = 0,
			bscatter = 0,
			bfixedp = 0,
			bsync = 0,
			bsync_l = -1,
			bdatas,
			cfr,
			scrollX,
			
			mcr = me.sheetoption.model.chart_option,
			swapaxis = mcr.swapaxis,
			useformula = mcr.useformula,
			maxchartresult = mcr.maxchartresult,
			timeseriesfield = mcr.timeseriesfield,
			useformatvalue = mcr.useformatvalue,
			measures = me.sheetoption.model.measures,
			f, _c, _uc, _fc,
			lbl, lbl_i, lbl_c, lbl_k, lblnames = {},
			mtitle,
			cf = [],
			sep = me.getSep();

		var tc = (data && data.length > 0) ? data[0].length : 0;
		var tr = (data ? data.length : 0);
		
		if (mresult.hidden_columns)
		{
			for (i=0; i < mresult.hidden_columns.length; i++)
			{
				hidden_columns[mresult.hidden_columns[i]] = 1;
			}
		}

		// line, spline, area, areaspline, column, bar, pie and scatter
		var rcs = me.rcs,
			spvalue = [];

		me.drawmode = "_modc_";

		if (rcs == "radar")
		{
			bpolarchart = 1;
		}
		else if (rcs == "fixedplacement")
		{
			bfixedp = 1;
		}
		else if (rcs == "syncchart")
		{
			bsync_l = (measures && measures.length > 1) ? measures.length : -1;
			bsync = bsync_l > 1 ? 1 : 0;
		}
		
		var coptions = [],
			coption;
		
        coption = me._create_option(mresult);
		
		me._prep_series_data(mresult);

		if (bpolarchart)
		{
			coption.chart.polar = true;
			coption.pane = {
				size: "80%"
			};
		}

		if (!bfixedp)
		{
			coption.chart.defaultSeriesType = IG$.getSeriesType(rcs);
		}

		// coption.subtitle = {text: "Source:"};
		
		if (mcr.enabledragselect)
		{
			//coption.chart.zoomType = "xy";
			//coption.chart.events = {
			//	selection: function(e) {
			//		me.selectPtsByDrag(this, e);
			//		return false; // Don't zoom
			//	},
			//	click: function(e) {
			//		me.unSelByClick(this, e, 1);
			//	}
			//};
		}
		else
		{
			//coption.chart.events = {
			//	click: function(e) {
			//		me.unSelByClick(this, e, 0);
			//	}
			//};
		}

        me._createAxis(coption);
			
		me._createYAxis(coption);

		me._plot_chart(coptions, coption);
		
		if (bfixedp)
		{
			coption.chart.type = "column";
			coption.plotOptions.column.grouping = false;
			coption.plotOptions.column.shadow = false;
			coption.plotOptions.column.borderWidth = 0;
			coption.tooltip = coption.tooltip || {};
			coption.tooltip.shared = true;
			
			var s1 = 0,
				s2 = 0;
			
			for (i=0; i < coption.series.length; i++)
			{
				coption.series[i].pointPadding = (coption.series.length > 2 ? 0.3 : 0.2) + (coption.series[i].yAxis ? s2 : s1) / 10;
				coption.series[i].pointPlacement = (coption.series.length > 2 ? (coption.series[i].yAxis ? 0.2 : -0.2) : 0);
				
				if (!coption.series[i].yAxis && s1 == 0)
				{
					coption.series[i].color = "rgba(165,170,217,1)";
				}
				else if (!coption.series[i].yAxis && s1 == 1)
				{
					coption.series[i].color = "rgba(126,86,134,.9)";
				}
				else if ((coption.series[i].yAxis && s2 == 0) || (!coption.series[i].yAxis && s1 == 2))
				{
					coption.series[i].color = "rgba(248,161,63,1)";
				}
				else if ((coption.series[i].yAxis && s2 == 1) || (!coption.series[i].yAxis && s1 == 3))
				{
					coption.series[i].color = "rgba(186,60,61,.9)";
				}
				
				if (coption.series[i].yAxis)
				{
					s2++;
				}
				else
				{
					s1++;
				}
			}
		}
		
		if (cf.length)
		{
			for (i=0; i < coptions.length; i++)
			{
				coptions[i].__cf = cf;
			}
		}

        me.render(coptions, mresult);

		return coptions;
    }
});